<template lang="pug">
.authType 
	.page__title 
		h2 login with
	.authType__content 
		#email.pointer(@click="$router.push('/emailAuth')") 
			img(src="../assets/img/email.png") 

		#loginGoogle.pointer(@click="googleAuth()")
			img(src="../assets/img/google.png")

		#loginFacebook.pointer(@click="facebookAuth()")
			img(src="../assets/img/facebook.png")

		#noAuthorize.pointer(@click="$router.push({ path: '/' })")
			img(src="../assets/img/offline.png")
	#registration.pointer(@click="$router.push({ path: '/reg' })") 
		button sign in with email
</template>

<script>
import firebase from "firebase/app";
import { mapMutations, mapActions } from "vuex";
/* eslint-disable */
export default {
	computed: {},
	methods: {
		...mapActions(["getData", "accessAllowed"]),
		...mapMutations(["SET_USER_NAME", "SET_NEW_STATE", "SET_INFO_MESSAGE"]),

		async logInWithProvider(provider) {
			const result = await firebase.auth().signInWithPopup(provider);
			this.SET_USER_NAME(result.user.displayName);
			const uid = await this.$store.dispatch("getUid");
			await firebase
				.database()
				.ref(`/users/${uid}/`)
				.on("value", snap => {
					const data = snap.val();
					this.SET_NEW_STATE(data);
				});
		},
		async googleAuth() {
			try {
				const provider = new firebase.auth.GoogleAuthProvider();
				await this.logInWithProvider(provider);
				this.accessAllowed("loged in with Google");
			} catch (e) {
				this.SET_INFO_MESSAGE(e.message);
				console.log("failed to access with Google", e.message);
			}
		},
		async facebookAuth() {
			try {
				const provider = new firebase.auth.FacebookAuthProvider();
				await this.logInWithProvider(provider);
				this.accessAllowed("loged in with Facebook");
			} catch (e) {
				this.SET_INFO_MESSAGE(e.message);
				console.log("failed to access with Facebook", e.message);
			}
		}
	}
};
</script>

<style lang="sass" scoped>
.authType
	display: flex
	align-items: center
	justify-content: center
	flex-direction: column
.authType__content
	width: 100%
	margin-top: 2rem
	display: grid
	grid-template-columns: 1fr 1fr
	justify-items: center
	align-items: center
	margin-bottom: 2rem
.page__title
	width: 100%
	text-align: right
	color: var(--akcentLight)
#loginGoogle, #loginFacebook, #email, #noAuthorize
	width: 95%
	height: 15rem
	display: flex
	justify-content: center
	align-items: center
	margin: 1rem 1rem .5rem 1rem
	border-radius: 10px
	background: linear-gradient(45deg, rgba(187,188,190,1) 0%, rgba(50,110,168,1) 32%, rgba(16,158,241,1) 100%)
	img
		width: 7rem
		height: 7rem
#registration 
	width: 97% 
	display: flex
	justify-content: center
	border: solid 2px var(--akcentLight)
	border-radius: 5px
	height: 5rem
	button 
		background: transparent
		color: var(--akcentLight)
		text-transform: uppercase
		font-weight: 800
</style>